// TypeScript - 
export interface Point {
  x: number
  y: number
}

/**
 * 
 * @param a  (x)
 * @param b  (y)
 * @param n  (n=2n>20<n<2)
 * @param segments 
 * @returns 
 */
export function superellipsePoints(a: number, b: number, n: number, segments = 256): Point[] {
  if (a <= 0 || b <= 0)
    throw new Error('a  b  0')
  if (n <= 0)
    throw new Error('n  0')
  segments = Math.max(4, Math.floor(segments))

  const pts: Point[] = []
  const exp = 2 / n // 

  //  [0, 2)  t
  for (let i = 0; i < segments; i++) {
    const t = (i / segments) * Math.PI * 2
    const c = Math.cos(t)
    const s = Math.sin(t)

    //  sign * |.|^exp
    const x = Math.sign(c) * Math.abs(c) ** exp * a
    const y = Math.sign(s) * Math.abs(s) ** exp * b

    pts.push({ x, y })
  }

  return pts
}

/**
 * SVG
 * @param points 
 * @param opts 
 * @param opts.close 
 * @param opts.precision 
 * @returns SVG
 */
export function superellipsePath(
  points: Point[],
  opts?: { close?: boolean, precision?: number },
): string {
  if (!points || points.length === 0)
    return ''
  const precision = opts?.precision ?? 3
  const fmt = (v: number) => Number(v.toFixed(precision))

  let d = `M ${fmt(points[0].x)} ${fmt(points[0].y)}`
  for (let i = 1; i < points.length; i++) {
    d += ` L ${fmt(points[i].x)} ${fmt(points[i].y)}`
  }
  if (opts?.close ?? true)
    d += ' Z'
  return d
}

/**
 * SVG
 * @param params 
 * @param params.a a
 * @param params.b b
 * @param params.n n
 * @param params.segments 
 * @param params.stroke 
 * @param params.strokeWidth 
 * @param params.fill 
 * @param params.widthPx 
 * @param params.heightPx 
 * @param params.padding 
 * @param params.precision 
 * @returns SVG
 */
export function generateSuperellipseSVG(params: {
  a: number
  b: number
  n: number
  segments?: number
  stroke?: string
  strokeWidth?: number
  fill?: string
  widthPx?: number
  heightPx?: number
  padding?: number
  precision?: number
}): string {
  const {
    a,
    b,
    n,
    segments = 512,
    stroke = 'black',
    strokeWidth = 1,
    fill = 'none',
    widthPx,
    heightPx,
    padding = 0.0,
    precision = 3,
  } = params

  const pts = superellipsePoints(a, b, n, segments)

  // viewBox
  let minX = Infinity
  let maxX = -Infinity
  let minY = Infinity
  let maxY = -Infinity
  for (const p of pts) {
    if (p.x < minX)
      minX = p.x
    if (p.x > maxX)
      maxX = p.x
    if (p.y < minY)
      minY = p.y
    if (p.y > maxY)
      maxY = p.y
  }
  // 
  const strokeOffset = strokeWidth / 2

  minX -= padding + strokeOffset
  minY -= padding + strokeOffset
  maxX += padding + strokeOffset
  maxY += padding + strokeOffset

  const vbW = maxX - minX
  const vbH = maxY - minY

  const pathD = superellipsePath(pts, { close: true, precision })

  const viewBox = `${minX} ${minY} ${vbW} ${vbH}`
  const widthAttr = widthPx ? ` width="${widthPx}"` : ''
  const heightAttr = heightPx ? ` height="${heightPx}"` : ''

  const svg = `<svg xmlns="http://www.w3.org/2000/svg"${widthAttr}${heightAttr} viewBox="${viewBox}" preserveAspectRatio="xMidYMid meet">
  <path d="${pathD}" fill="${fill}" stroke="${stroke}" stroke-width="${strokeWidth}" vector-effect="non-scaling-stroke" />
</svg>`

  return svg
}

/**
 * 
 * @param a 
 * @param b 
 * @param n 
 * @returns 
 */
export function calculateArea(a: number, b: number, n: number): number {
  // 
  const segments = 1000
  const points = superellipsePoints(a, b, n, segments)

  let area = 0
  for (let i = 0; i < points.length; i++) {
    const p1 = points[i]
    const p2 = points[(i + 1) % points.length]
    area += (p1.x * p2.y - p2.x * p1.y) / 2
  }

  return Math.abs(area)
}

/**
 * 
 * @param a 
 * @param b 
 * @param n 
 * @returns  {minX, maxX, minY, maxY}
 */
export function getBounds(a: number, b: number, n: number) {
  const points = superellipsePoints(a, b, n, 256)

  if (points.length === 0) {
    return { minX: 0, maxX: 0, minY: 0, maxY: 0 }
  }

  let minX = Infinity
  let maxX = -Infinity
  let minY = Infinity
  let maxY = -Infinity

  for (const p of points) {
    minX = Math.min(minX, p.x)
    maxX = Math.max(maxX, p.x)
    minY = Math.min(minY, p.y)
    maxY = Math.max(maxY, p.y)
  }

  // Infinity
  if (
    !Number.isFinite(minX)
    || !Number.isFinite(maxX)
    || !Number.isFinite(minY)
    || !Number.isFinite(maxY)
  ) {
    return { minX: -a, maxX: a, minY: -b, maxY: b }
  }

  return { minX, maxX, minY, maxY }
}
